expenses-filter {
  color: white;
  padding: 0 1rem;
}

.expenses-filter-control {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: space-between;
  margin: 1rem 0;
}

.expenses-filter label {
  font-weight: bold;
  margin: 0.5rem; 
  font-size:20px;
}

.expenses-filter select {
  font: inherit;
  padding: 0.5rem 3rem;
  font-weight: bold;
  border-radius: 6px;
  border: none;
  outline: none;
}
